document.addEventListener('DOMContentLoaded', function () {
    mobileMenu();
    setActiveMenuItem();
    redirectFromHTML();
  });
  
  function mobileMenu() {
    const btnMenu = document.querySelector('.icon_menu');
    if (btnMenu) {
      btnMenu.addEventListener('click', function () {
        const menuItems = document.querySelector('.menu');
        menuItems.classList.toggle('active');
      });
    }
  }
  
  function setActiveMenuItem() {
    const activePage = location.pathname;
    const menuItems = document.querySelectorAll('a');
    menuItems.forEach(function (menuItem) {
      if (menuItem.getAttribute('href') === activePage) {
        menuItem.classList.add('activ');
      }
    });
  }
  
  function redirectFromHTML() {
    const routes = [
      { from: '/index.html', to: '/' },
      { from: '/nosotros.html', to: '/nosotros' },
      { from: '/renders.html', to: '/renders' },
      { from: '/proyectos.html', to: '/proyectos' },
      { from: '/contacto.html', to: '/contacto' }
    ];
  
    const currentPath = window.location.pathname;
    const redirect = routes.find(route => route.from === currentPath);
  
    if (redirect) {
      window.location.replace(redirect.to);
    }
  }
  